          name: "心系海外员工 情暖后方家庭，五建集团",
          description:
            "自2021年起，设计院结构所职工钱文涛赴乌兹别克斯坦三座银行总部大楼项目驻场从事设计管理工作。在这近三年半的时间里，结构所始终能够积极做好钱文涛家属的后勤服务保障和日常关爱工作。\n       真心换真心。为了能够保障两老人们的正常生活，给他们提供及时的帮助，结构所主动要到了老人的联系方式，轮流通过微信、电话、上门探望等方式联系钱文涛的父母。“阿姨、最近家里都好吧，家里有没有什么需要我们做的事情？”，“阿姨、今天在家吗，马上要过节了，我们去看看您和叔叔”经常的交流和通话也成为了他们日常工作的一部分。在这段日子里，累计上门探望20余次，陪同亲属到医院看病2次，以真心换得了钱文涛家属对公司项目的理解和支持。真心换真意。为了能够让钱文涛安心工作，减少对国内家里人的牵挂，经常对钱文涛说：“小钱、家里有什么困难，有什么需要帮助的，随时打招呼，公司离你家也比较近，随时都能过去帮忙。”此外，还积极组织所内同事对钱文涛在工作上提供技术支持，使其在项目上遇到的技术问题能够及时的解决，保障其工作的顺利开展，钱文涛对公司的归属感和认同感也更加强烈，团队凝聚力也得到了很大的提升！\n       凝心聚力、奋发有为，整个团队用他们的实际行动关心、关爱好身边的每一位同事，努力将集团打造成为国际一流的建筑全生命周期服务商！",
        },
        {
          name: "抗震救灾勇担当，建设精锐使命必达，七建集团",
          description:
            "2023年12月18日23时59分，甘肃省临夏州积石山县发生6.2级地震。甘肃、青海受灾严重，有上百人遇难、近千人受伤，部分房屋、水、电、路等基础设施严重受损。地震发生后，七建集团第一时间响应号召，安排西安分公司组织力量前往青海开展抗震救灾工作，在当地抗震救灾指挥部的指挥下，全力克服诸多困难，争分夺秒、统筹协调，第一时间运送物资至灾区，顺利完成驰援任务。\n       自12月21日下午接到援助灾区捐赠一批箱式板房的任务后，七建集团西安分公司挺膺担当，委派两名管理人员于12月22日搭乘第一班航班赶往西宁，全力寻找箱式板房资源。由于西宁天气已进入深冬，箱式板房许多厂家已经停产，导致物资调集困难。两位同志多处辗转，当天完成了箱式板房的预订采购并全力找寻运送车辆。12月23日下午2点，第一批集装箱5车15套装车完成，立刻发往灾区。根据当地要求，需要将箱式板房分别运送至不同乡村的多个安置点，而不是简单送至集中安置点，这对车队的运输任务又增加了困难。由于灾区路况较差，多为山路、急转，运送卡车无法进入乡村小路，到达各村后还需另寻小车进行转运，且安置点分散，第一批箱式板房送往西沟乡1个安置点3间、转导乡4个安置点12间，于当天凌晨2点左右全部完成吊运安装工作。至25日17时，七建集团分三批次，共计运送54套箱式板房前往受灾的海东市民和回族土族自治县，点对点地运往西沟乡、转导乡、甘沟乡、王家村、接管龄村、三湾村等多个受灾乡村，并完成所有板房的吊运安装工作，确保附近灾民群众在寒冬时节能够及时入住。\n       灾难无情、人间有爱，一方有难、八方支援。上海建工七建集团以“建设精锐”的拼搏精神，积极助力打赢此次抗震救灾攻坚战，用实际行动展现了新时代国有企业的责任与担当。",
        },
        {
          name: "项目党建联建架起“连心桥”温暖“邻里圈”，安装集团",
          description:
            "把党旗插在工地现场，把上海建工的红色基因带到项目一线，让“红色引擎”激发奋进动能，用高质量党建推动项目高质量建设，成为安装集团第六工程公司近年来打通周家嘴路隧道、北翟路地道、北横通道东段等本市7条隧道类工程的重要保障。\n       2021年1月，江浦路隧道机电工程正式开工。在施工期间，高压线路要穿越附近的小区，项目部在踏勘时，发现该小区有一段围墙是铁丝网。询问后才得知，本来这里要建一排商铺，因此就搭建了临时“围墙”，后来商铺没有开出来，“围墙”却临时搭设着。因为高压线路有开关站设在此地，项目部和所在辖区街道通过党建联建，商讨解决办法，最终决定来承建这段原本不属于工程范围的围墙，解除小区群众的心头之患，还老百姓一个安全感。\n       把工作做早、做细、做实，党建联建的效果也就出来了。那一年，强台风“烟花”来袭，为了保证安全，项目部连夜要把所有人从活动房里撤出来。撤出来没问题，撤到哪里去却成了头等大事。夜间值班不可或缺，刚刚成立的抢险队必须待命，农民工的集中居住需要解决。如果安置点过远，就会耽误了现场应急，一时间，项目部有点犯难。就在这时，栖山居委会干部得知了项目部的难处，特地把居委会的活动室、阅览室、会议室都腾了出来，还特地开放了淋浴室，备妥了微波炉、热水壶等一应生活用品，解了项目部的燃眉之急。\n       台风走后，由于社区的老龄化程度较高，有的老人对于家用电器的维修可以说是一窍不通，不知怎么维修、也不知怎么找专业维修人员。项目团队通过组建党员志愿服务队，主动深入关心项目沿线居民，切实为沿线居民提供家电维修、水电服务等，深受居民欢迎。\n       把“项目党建就是工地上的‘红色引擎’，做实了就是生产力，做细了就是向心力，做强了就是战斗力”的理念融入项目建设全过程，深化党建联建共建，真心实意为社区居民纾困解难，架起了企业与社区的“连心桥”，也凝聚了建设的向心力。\n       隧道再长，难关再险，总能冲破黑暗，迎接光明。",
        },
        {
          name: "学雷锋水电维修服务队连续十二年为社区居民排忧解难，安装集团",
          description:
            "2023年4月，安装集团第三工程公司收到了一封“特殊”的感谢信。这封手写信淡墨素笺，纸短情长，字里行间充满了对第三工程公司学雷锋水电维修服务队的谢意。\n       手写信来自家住科汇小区的童老先生。当时，因为家中下水道不畅通，老先生烦恼多时。在2023年三月的学雷锋活动日中，第三工程公司学雷锋服务队曾为他上门查看并疏通了管道。然而不到一个月，家中的下水道又出现了同样的问题。无奈之下，他再次联系到了第三工程公司寻求帮助。本着有求必应、负责到底的想法，服务队再次上门进行了全面检查，理清来龙去脉后，发现原来是由于老旧管道结构不合理导致的堵塞。为此，服务队在疏通管道的基础上，花了一番精力，对下水道进行了改造，彻底解决了这个困扰老先生多年的问题。\n       2024年安装集团是学雷锋水电维修服务队为社区居民排忧解难的第12个年头。为了更好地服务社区居民，这些年，服务队结合自身特长，集合了通风、电气、管道等多个专业骨干，满足不同方面的需求。12年来，服务队共计为73户居民提供了贴心服务，虽然只是换灯泡、通水管、修龙头这些举手之劳的小事，但实实在在地为大家解决了一项又一项烦心事、揪心事，周到、热情、细致的服务获得了社区居民们的一致好评。\n       这些年，第三工程公司还加入了徐家汇街道市级文明单位社会责任联盟，并建立了志愿者活动的长效机制，鼓励职工群众大力弘扬雷锋精神，投身社会实践，将新时代上安人的“奋斗者精神”转化为服务社区的实际行动。服务队曾入选徐家汇街道公益服务手册，事迹被刊登在《新民晚报》社区版，职工还荣获徐汇区优秀志愿者称号。\n       “学习雷锋好榜样”的歌声穿越半个世纪依旧响亮，在这响亮的歌声里，上安“雷锋”接力前行，让爱心在行动中播撒，让正能量在传播中延续，为雷锋精神赋予新的时代特色。也正因为这份坚守，志愿服务的点滴微光，在这里，在那里，聚成一束光，发出一团热，照亮了大街小巷，温暖了人心。",
        },
        {
          name: "情系养老院，关怀暖人心——申宝公司“红旗青年志愿队”长期服务上海建阳养老院，基础集团",
          description:
            "“红旗青年志愿队”于2016年在申宝公司党支部、工会以及团小组齐心协力下共同组建。这支充满活力与爱心的队伍，长期与上海建阳养老院的医护人员以及高龄且生活相对困难的老人们保持着紧密且高效的沟通。他们定期了解老人们的生活状况，及时掌握其健康情况，而后贴心地为老人们配送药物、食物等生活必需品。同时，在每一次的慰问前，队员们都会精心策划，准备丰富多样的精彩节目，只为给老人们带来难忘的体验。\n       在疫情防控的艰难时刻，志愿队的队员们不定期地将老人们急需的生活用品与常用药品送到养老院医护人员的手中，并且始终不忘深情叮嘱老人们要保重身体。每逢春节、重阳节等重大节日，志愿队更是提前精心采买好慰问物资，亲自将其送到老人手中，主动关心老人们的生活起居，诚挚地送上节日慰问。老人们对志愿队的付出满怀感激之情。\n       在今年中秋节时，申宝公司组织了“饺子飘香迎中秋”活动，邀请了养老院的老人们一起包水饺。老人们兴致勃勃地参与其中，分享着包水饺的技巧和生活趣事。当热气腾腾的水饺出锅时，老人们品尝着自己的劳动成果，脸上洋溢着幸福的笑容。在这个特殊的日子里，水饺不仅是美食，更是传递温暖与关爱的纽带，让老人们感受到了浓浓的节日氛围和家的温馨。\n       此类充满温情的活动，让老人们真真切切地感受到了来自申宝公司“红旗青年志愿队”的暖心关怀。这不仅充分彰显了铁军青年敢于担当、积极履行社会责任的卓越品质，更是成为树立良好企业品牌和形象的重要载体与平台。在未来的日子里，申宝公司“红旗青年志愿队”将继续秉持着“奉献、友爱、互助、进步”的志愿精神，不断丰富慰问活动的内容和形式，为养老院的老人们带去更多的关爱和帮助，用自己的实际行动为老人们撑起一片温暖的天空，让爱心在敬老院中永远绽放。",
        },
        {
          name: "风雨逆行，勇担职责，绍兴市轨道交通建设工程应急抢险总队支援古城内府山闸区域河水抽排，基础集团",
          description:
            "2021年6号台风“烟花”汹涌来袭，绍兴市轨道交通应急抢险总队严阵以待，时刻关注雨情、工情、水情，有序开展各项应急检查，全面进入防汛备战状态。7月26日，狂风劲吹，顷刻间暴雨如注，绍兴城区多条河流水位持续上涨，严重危及人民群众生命财产安全。\n       风驰电掣，披坚执锐\n       风雨交加，奏响应急响应的号角。应急抢险总队接到绍兴市轨道交通集团支援防汛通知后立即行动，迅速执行应急预案部署，出动发电机、大流量水泵、随车吊等应急物资，以雷霆之势奔赴绍兴市越城区胜利西路环城路路口，前往古城内河府山闸执行河水抽排任务。\n       是时，河道积水已呈严峻之势，若不及时展开抽水作业，雨水随时会顺着下水道倒灌进入居民区内，救援工作刻不容缓。应急抢险总队深知责任重大，抵达现场后第一时间在指定位置部署3台大流量水泵，妥善稳固水带，严防死守水位，分组值守发电机、开关箱等关键设备，确保抽排作业稳定有序进行，与消防等单位并肩作战，共同面对河道积水带来的严峻挑战。面对滔滔积水，他们眼神坚定，心中只有一个信念：尽快排除积水，保障人民生命财产安全。他们不畏艰难，不顾烈日炎炎与潮湿闷热，操作着一台台设备，让抽水泵发出有力的轰鸣声。那声音，仿佛是他们为人民奏响的守护之歌。他们的身影，在抢险现场忙碌穿梭，成为了一道亮丽的风景线。在救援各方的共同努力下，经过36小时连续抽排，河流水位下降至稳定区间，有效缓解古城区防涝压力，应急救援任务顺利完成。\n       风清雨霁，勇毅前行\n       越是关键时刻，越有担当作为，应急抢险总队肩担国企社会责任和应急救援使命，秉承“预防为主、常备不懈、快速反应、抢险救援”的工作宗旨，将日常演练应用于实战，在关键时刻发挥自身专业优势，展现基础铁军的风采，奋力筑牢一道坚固的“红色堤坝”，为保障人民群众生命财产安全而奋勇拼搏。",
        },
        {
          name: "专业抢险队迎战台风“普拉桑”，机施集团",
          description:
            "今年第14号台风“普拉桑”2024年9月19日夜在奉贤区沿海登陆，强台风给全市人民的生产生活带来了巨大影响，奉贤区更是普降70年难遇的特大暴雨。在接到了市住建委的通知后，整装待命的机施集团应急救援队立刻组织精兵强将和设备台班驰援，第一时间奔赴抢险救灾第一线。\n       救援队20日晚间完成了第一抢险位置奉贤区海杰路的抢险任务，确保了道路的安全有序。当晚23时左右，救援队抵达第二抢险位置，奉贤区四团镇普洛斯奉贤海港物流园附近。经过了一整天的暴雨侵袭，该处一工地基坑被淹，城市道路也出现大面积积水。于深夜逢险情，机施集团应急救援队用强烈的使命感和精湛的专业能力，展现了国企的责任担当，出动吊车等施工车辆，启动发电机、水泵等应急抢险设备和物资，全队开足马力，全力投入抢险中去。风雨中全员“泡”在现场，无人退缩，经过六个多小时的奋战，累积排水超过3000立方，至第二日早六时许成功完成基坑和路面排水任务、以及道路清障疏通等工作，在早高峰到来前顺利还路于民。\n       9月24日，机施集团收到奉贤区建设和管理委员会、奉贤区交通委员会发来的感谢信，对救援团队深入受淹区域道路街区不分昼夜抢排除险，为助力加快消除特大暴雨影响，保障道路交通安全、畅通、有序所付出的努力致以诚挚谢意。\n       机施集团应急抢险队是于2019年组建的一支专业化抢险队伍。队伍配备专业的人员、设备、车辆，有多个物资仓库，每年参与市、区以及申通、水务等条线组织的实战演练活动十余次，为妥善处置各类突发事件奠定了坚实基础。此次面对台风“普拉桑”的抢险任务，团队经受住了考验，做到了“拉得出，打得响”，集中体现了机施集团面对重大险情的处置应对、协调合作和解决能力。",
        },
        {
          name: "“情系沪饶 助梦未来”园林集团金秋助学系列活动，园林集团",
          description:
            "1995年，经时任上海市人大常委会副主任孙贵璋和江西省上饶市政府牵头，园林集团出资在上饶市援建了一所希望小学。1996年，金秋开学季，“上饶市沪饶友谊小学”正式挂牌，开始迎接第一批莘莘学子。2002年，园林集团的志愿者们第一次来到沪饶友谊小学，为孩子们组织了一场助学捐赠活动，种下了园林集团在当地开展助学系列活动的第一颗种子。\n       2019-2024年，这六年间，园林集团几乎每年不间断开展上饶市沪饶友谊小学助学系列活动，通过多种形式，不断拓展延伸此项活动的宽度、广度、深度，共计近300余人次的学生感受到了园林集团的温暖传递。\n       2019、2022、2024三年，园林集团前后组织60余位志愿者来到学校，送去了各类学习、生活用品，还对学校建档卡贫困学生进行慰问。志愿者们结合自身园林、古建、艺术等专业特色，给孩子们带去了又有趣又有意义的课程，诸如“我们身边的植物”、微缩水世界、音乐故事课、少儿简笔画、“我们心中的校园”等等。课后，志愿者们还与孩子们开展拔河比赛、才艺表演等丰富多彩的活动，拉近与孩子们的距离。\n       2021、2023这两年，园林集团每年邀请孩子们到上海来进行暑期研习活动，先后前往园林集团本部大楼、长三角一体化绿色科技示范楼、中共一大会址、中共四大纪念馆、上海科技馆、上海自然博物馆、上海海洋水族馆、上海中心大厦等地参观研学。志愿者们全程陪同，认真细致地实地讲解，大大开拓了孩子们的眼界，让孩子们学习到了很多书本之外的知识。\n       沪饶友谊小学的时光漫长又短暂：一张张稚嫩的脸庞，充满了对知识的渴望；从一个个懵懂的孩童，长成了一位位风华正茂的少年。孩子们与大哥哥大姐姐们也结下了深厚的友谊，一封封来自上饶的书信虽然笔迹稚嫩，但满含对志愿者们的感谢与深情。\n       一年又一年，园林集团在沪饶友谊小学种下有温度、有梦想的种子，静待萌发。三十年情系沪饶，三十年情谊弥坚，通过“情系沪饶 助梦未来”助学系列活动，园林集团不断彰显国有企业的社会责任担当，为乡村振兴等国家战略贡献力量。",
        },
        {
          name: "践行党员义务，坚守党员初心，建工设计研究总院",
          description:
            "李化国，男，中共党员，建工总院第三事业部部长助理兼第三设计研究院副院长（主持工作），始终秉承党员使命，为周边的家人、同事、朋友做好表率工作。\n       2022年三月一场突然袭来的“倒春寒”疫情打乱了我们的生活节奏，李化国同志所在的小区是首批隔离的小区，在隔离期间，其积极遵守街道和居委的要求，做好居民的居家工作。同时，作为一名党员，他及时快速的向居委党支部汇报自己的党员身份，为小区防疫作出自己的贡献。生命重于泰山，疫情就是战斗，防控就是每个人的责任和义务。被管控在家时，作为一名党员，响应绿庭居委会的志愿者号召，积极报名志愿者服务，穿上白色抗疫服，化身“大白”，组织协调大家开展核酸检测工作，维护核酸检测秩序，经常连续工作6个小时以上，直到核酸检测工作完成。同时，他主动向居委党组织报到，听从调配，根据需要，参与到小区巡逻监督工作当中，巡查小区人员下楼和戴口罩情况。遇到生活物资发放的时，又充当小区“快递员”，将生活物资发放到居民门口。 \n       由于小区体量庞大，约4100户，1万2千人左右，在疫情来临时，需要大量的志愿者帮忙协助。李化国鼓励爱人一起参与疫情防控，担当抗疫志愿者，参与小区的防疫防控工作，也为孩子做了一个好榜样。抗“疫”期间，李化国作为普通居民中的一员，不忘初心及时亮出党员身份，帮助群众开展防疫工作，将满腔热情投入到复杂艰巨疫情防控工作中，任劳任怨、主动担当，用实际行动谱写了一曲最美的战疫之歌。",
        },
        {
          name: "不啻微芒，造炬成阳，在坚守和奋斗中践行初心使命，建工设计研究总院",
          description:
            "张铮鸣同志2010年毕业后进入建浩公司从事造价咨询，至今已有14余年，主要负责部门的内部管理工作，同时也作为具体项目的负责人加入项目的第一线。一直以来她工作兢兢业业、作风严谨正派，在工作中不断努力学习、拓展专业技术知识，确保圆满完成所有工作。\n       自2015年起兼职工会职务后，她积极配合工会工作结合自身所在部门组织开展一系列活动，以职工群众切实需求为导向，成为职工群众和公司党政的桥梁和纽带，团结带动周围职工，加强学习与研究各项理论政策业务，不断提高工会作风建设和效能建设，服务公司发展大局。\n       在2022年疫情期间小区闭环管理后，张铮鸣成为了小区居委的骨干志愿者，作为下沉党员始终冲锋在前。为保障居民物资供应，她更是承担起小区物资接收和分发工作的牵头人。\n       为了保障小区居民物资及时运送分发，她召集了一支志愿者队伍，随时待命，为在最快时间内完成肉蛋奶等新鲜物资的分发提供了有力保障。每天只有等分发工作完成后她才能在内心上放松下来。无论刮风下雨还是用餐时间，她与她的志愿者团队都是随叫随到随时待命直到疫情结束。\n       自2022年底起她参与了联合国儿童基金会的捐赠活动，想通过自己给需要帮助的人尽一份微薄之力。作为一名青年党员，她始终尽自己所能，认真踏实、无惧无畏、为抗疫贡献自己的全部力量，以实际行动，在工作和生活中践行一名共产党员应有的作为和担当。上海杨浦公众号以及杨浦区妇联公众号在2022年5月19日和2022年6月2日的各自公众号中分别发文《邻家姐妹团，铿锵玫瑰战疫线》、《他们牵线搭桥，守好居民“菜篮子”》对张铮鸣同志在疫情期间的优秀表现给予肯定。",
        },
        {
          name: "海外救援的最美逆行者，建材科技",
          description:
            "马海洋，中共党员，现任上海材二科技有限公司南通市通州沪盛混凝土有限公司生产运作部经理。在工作之余，他也是南通市蓝天救援队的一名成员，目前得到了国家五级急救员资质。\n       2023年2月，土耳其发生7.8级大地震，马海洋同志跟随蓝天救援队经过40多个小时辗转顺利抵达土耳其马拉蒂亚地震灾区，负责房屋破拆任务。为了多寻找一丝生的希望，他与队友每天工作将近17个小时，更是一次连续作战了36个小时。救援任务结束后，马海洋及其他队员们纷纷将食物及部分救援物资留在了地震灾区，并于2月20日平安凯旋。同年8月，河北涿州遭遇持续强降雨，部分地区出现严重内涝。防汛形势严峻，导致众多百姓被困。马海洋同志应征前往，赶赴受灾地区抢险救援，和其他队员一道协助开展人员转移和物资转送工作。在历时4天的救援中，马海洋同志同其他救援队员累计转移被困群众209人，并成功运送了一批生活物资。\n       今年以来，马海洋已累计参与了3次水上救援打捞行动，每一次都面临着复杂多变的水域环境和紧迫的救援时间限制。他凭借着过硬的技能和冷静的判断，成功救助了5名落水人员，将他们从死神手中拉了回来。这些英勇的事迹不仅挽救了宝贵的生命，也赢得了社会各界的广泛赞誉和尊敬。",
        },
        {
          name: "无偿献血传爱心，夜间巡湖保生态，建材科技",
          description:
            "韦江余，是建材科技集团苏州公司一名普通的全国化职工。3800毫升无偿献血，3000元的疫情捐款，2807小时志愿巡湖。这些温暖的数字，描绘出韦江余的奉献人生。岗位上的他是值得信任的同事，岗位外的他是热心的昆山市优秀志愿者，曾荣获昆山市志愿者五星级荣誉证书。在过去的八年里，他每年都会主动前往市区爱心献血站无偿献血，累计献血共10次，总计献血量3800ml，曾荣获2021年-2022年昆山市无偿献血奉献奖银奖。在他心中，献血是对自我奉献精神的践行和对他人生命的尊重。  \n       除了献血外，韦江余还加入了昆山“大禹公益”志愿团队，尽自己所能保护苏州阳澄湖的鱼水生态。响应昆山市“反电、毒、炸鱼”的号召，他在每日工作结束后积极配合巴城镇渔政部门，巡逻在巴城镇各河道，劝导和举报那些电鱼、网鱼、毒鱼、炸鱼等各类违法行为。在巴城镇大大小小的河道湖泊，都留下了韦江余和同伴志愿者服务的足迹。“帮助别人也是帮助自己，为他人排忧解难的同时，也是在成就自我。”正是这样的人生信条使得韦江余始终热衷于参加各类志愿活动。他用自己的实际行动深刻践行了“奉献、友爱、互助、进步”的志愿者精神，真正诠释了乐于付出、助人为乐的奉献精神，展现了志愿者服务社会、展现自我的时代风采。",
        },
        {
          name: "外经控股厄特分公司改善当地民生助力社区和谐可持续发展，外经集团",
          description:
            "“以前，我们只能去村子旁边的湖泊打水。自从中国企业来到这里，不仅开始建设发电站、解决我们的用电难题，还专门为我们修建了净水设备，接水方便，水也干净多了。China good！”贝雷扎的村民雅玛尼感慨地说道。2023年6月，由外经集团厄特分公司承建的贝雷扎发电站项目在厄立特里亚首都阿斯马拉的贝雷扎地区开工建设。作为中厄合作的重点内容，该项目建成后将有力缓解该国首都地区电力紧缺状况，同时，该项目也是厄特惠民生、促发展的战略项目，将大力推动当地经济发展、改善民生，为当地百姓带来中厄友谊之光。\n       贝雷扎村位于厄特首都阿斯马拉市区往北约9公里，海拔约2400米。由于地处高原，当地只能靠雨季的雨水汇集到湖泊或水库来满足当地民众的用水需求，生活用水严重匮乏。加上缺少必要的清洁和消毒，村民们的身体健康面临极大的隐患。在发电站项目建设之余，外经集团根据当地村民缺水，尤其缺饮用水的情况下，在改善当地村民干净用水、造福当地民生方面进行了探索。\n       贝雷扎发电站项目经理高如意介绍，项目团队在靠近项目的湖泊建设取水泵房，修建管路到达项目的生活营地，并同时在营地和电厂内建设了净水房及配套水箱，分别投建了两台大型净水设备，开放给当地居民来打干净用水，改善了当地用水卫生和安全，获得了当地村民和社区的一致好评。除此之外，外经集团投入资金，为贝雷扎村无偿修建道路，极大改善了村民出行条件；进行文化交流，资助贝雷扎村教育事业和修缮村里足球场；开展爱心捐赠和义卖活动等等。\n       厄籍员工特塞玛·格鲁尔是贝雷扎发电站项目人力资源负责人。他谈到，自开工以来，项目先后解决了400多人次当地百姓的就业问题，并通过为当地员工提供安全和现场生产的基础技术培训，为当地培养了大批建设人才。从传授技术到基础设施建设，再到文化教育交流，外经集团积极投入到中厄合作项目，扎扎实实地帮助当地百姓解决问题。在项目执行过程中，项目团队制定了就业、技术人才培训计划，安排中国师傅对当地员工“帮扶带教”，手把手教授厄特员工，把零基础、低技能人员培养成为项目主力员工，具备了专业技能的大型设备的驾驶员、维修员，成长为项目现场专业的瓦工、钢筋工、木工、安装工、焊工、管工等专业技能人才，让他们掌握一技之长，切实改善了当地老百姓的生活和就业问题。“中国企业不仅高质量、安全施工，同时也积极改善当地百姓民生，为社区和谐发展作出贡献，助力厄特可持续发展。”特塞玛·格鲁尔说。",
        },
        {
          name: "保卫部职工奋勇处置居民区火情，天住集团",
          description:
            "6月7日上午11时左右，三盛里居民区突传来呼救声。闻声之际，小区二楼窗户已见明火，就在火势迅速蔓延的时候，崔向超、李晓明、赵焱三名同志已经拿起了灭火器，竟没有丝毫犹豫，第一时间冲到了火灾现场。到达现场后，三人迅速找到起火点，发现厨房燃气灶上油锅火势迅猛，此时已经引燃了周围的大量杂物，火势迅速蔓延，烟雾也很快充斥了整个房间，而就在小区不足5米之处还有大量的绿植。\n       三人当机立断，冒着浓烟，直冲厨房，将正在燃烧的纸板、油锅等杂物迅速清理下来，而就在这样的危急时刻，三人展现出了良好的专业素养，为了避免火灾引起的燃气爆炸事故，在清理起火点后的第一时间他们就检查了燃气阀，再使用灭火器进行灭火。最后，在三人通力协作之下，火势得到了有效控制，最终保障了人民群众生命财产安全。\n       崔向超、李晓明、赵焱三位同志，面对危险不退缩，冲锋在前，将国家利益、群众利益放在首位，用挺身而出的实际行动彰显了天住人的担当本色，用自己的实际行动践行“全心全意为人民服务”的宗旨，展现了一个平凡工作者不平凡的勇气和担当。\n       2024年6月，天津市公安局和平分局确认崔向超等人在和平区马场道三盛里25号楼灭火行为为见义勇为行为，并颁发确认证书。同时，天津市和平区见义勇为协会为三名同志各颁发奖励金1000元。",
        },
        {
          name: "天津市津南区八里台镇局部地面沉降工作应急抢险处置，天住集团",
          description:
            "2023年5月31日，房鉴院接到上级通知，津南区八里台镇发生局部地面沉降，院长曹宇立即成立了应急抢险工作组并担任组长，带队赶赴津南现场，全力配合天津市处置津“5.31”道路地面沉降工作指挥部开展应急处置工作。\n       按照指挥部的人统一安排，房鉴院负责受影响最严重的四栋高层和大量多层住宅的安全排查工作。时值酷暑，事故楼内停电停梯，工程师每日通过步梯逐层踏勘，排查数百个点位。内业工程师，逐一核查原始资料，进行建模计算、抗倾覆验算、建立一楼一册评估档案。大家通宵熬夜、争分夺秒，在三天内完成了受影响较为严重的20余栋建筑的现场踏勘、原设计资料核查、建模计算、抗倾覆验算等工作，编制阶段性评估意见表80余份，向指挥部提供了建筑当前安全状态的第一手资料，为后期工作打下了良好的基础。由于表现突出，经批准，房鉴院副总工程师金明昌出任市级专家组成员，参与制定了注浆加固、斜井封堵、回填砂土等有效抢险措施，编制了《强降雨、台风对房屋安全性影响分析报告》《高层建筑超规范倾斜率预警限值的分类及应急措施》等多份材料，为应急处置工作提供了专业的指导意见。\n       为百姓解困，为政府分忧，是国有企业义不容辞的责任。居民楼出现地基沉降和裂缝，给业主带来了极大地困扰和不安。为了让受影响的业主安心安业，指挥部多次召开了群众见面会，房鉴院副总工程师金明昌作为房屋结构专家，向群众汇报了当前房屋结构安全的总体情况，对于群众关注的地面沉降程度、房屋倾斜情况、沉降发展趋势等问题，用老百姓通俗易懂的语言进行了解答，耐心疏解老百姓的所思所想所盼，积极配合协助指挥部完成疏散范围内业主的签约工作。\n       在市委市政府的坚强领导下，在市指挥部的统筹调度下，房鉴院与其他单位通力协作，经过近7个月的努力，受灾居民得到妥善安置、修缮工作有序推进、群众思想情绪稳定，实现了“安全、安居、安稳”的目标。\n       2024年1月，津南区党委和区政府送来感谢信，对房鉴院在抢险工作表现出的大局意识、责任担当、初心使命和奉献精神表示高度认可和由衷感谢。",
        },
        {
          name: "山海有界，大爱无疆\n       ——海外部援外项目情暖非洲孤残儿童，海外事业部",
          description:
            "援莱索托马塞卢地区医院和眼科诊所项目是中国对外援助工程，医院地处非洲南部的“国中国”莱索托马塞卢市区，占地面积超2.3万平方米，拥有200余个床位，建成后将为莱索托人民提供先进的医疗保障，促进当地医疗条件改善升级，助力莱索托构建更完善的公共卫生体系。项目实施中大量使用属地化员工，高峰时期属地化用工达到200余人。\n       2023年7月的莱索托寒风刺骨，市区最低温度达到-10℃。项目部通过属地化员工了解到他们村里有一家孤儿院和一家残疾儿童之家。考虑到马塞卢医院项目也设有儿科，为了更好地了解当地孩子的日常生活现状和基础医疗条件，项目管理人员跟随当地司机前往孤儿院实地走访。\n       经走访，项目部了解到马迪布森孤儿院没有固定经济来源，只有一位行动困难的老人勉强维持着37名孤儿的日常管理，年纪较大的孩子同时承担起照顾年幼孩子的任务。这些孤儿没有御寒衣物，大冬天还光着脚丫跑来跑去，日常餐食和生活用品无法得到基本满足。孤儿院内设施陈旧，屋顶漏水、四壁透风，卧室仅有十来张床铺，多数小孩仍打地铺睡在冰冷的水泥地上。\n       走访后，项目部立即发起爱心募捐活动，项目管理人员踊跃捐款。7月15日，项目全体管理人员带着用募捐款购买的面粉、食用油等必需品，牛奶、饼干等零食，棉衣、鞋子、绒线帽等衣物，以及学习用品来到马迪布森孤儿院，给孩子们送去冬日的温暖。大家为孩子们穿上新衣服，鼓励他们好好学习，将来用知识改变命运。孩子们脸上露出灿烂的笑容，热情地在纸片上用稚嫩的文字写下对上海建工的感谢和祝福，款款的人间温情驱散了冬日的严寒。\n       2023年圣诞节，项目管理人员再次发起关爱行动，通过自发募捐的形式慰问了马塞卢圣安琪拉残疾儿童之家。此次慰问，项目员工除了给孩子们送上食品衣物、学习用品等，还现场教授孩子们简单汉语词汇，讲述中国优秀传统文化故事。孩子们听得津津有味，用新学的“你好”、“谢谢”等词汇热情互动，并向项目人员朗诵自己写的诗歌、展示自己创作的绘画作品并表演当地特色舞蹈。\n       山海有界，大爱无疆。援莱索托马塞卢地区医院和眼科诊所项目部在高质量完成这项“一带一路”重大工程之余，始终不忘承担国有企业社会责任，坚持用真心、爱心加固中非人民友谊，让援外工程成为讲好新时代中国海外故事和构建人类命运共同体的“扬声器”。\n       ",
        },
        {
          name: "事不避难，投身一线，全力保障项目供应，电商公司",
          description:
            "2023年3月电商公司陆董接到集团主要领导布置的紧急保供任务，要求电商公司即刻安排进驻总承包部“818工程”项目，发挥在“金山方舱”物资保供中积累的宝贵经验，利用优势供应商资源全力做好项目整体移交前的物资供应。由于该项目为市重大工程，且涉及保密要求，公司从各部门抽调政治立场坚定、业务水平过硬的骨干力量组成“特供项目保障组”。在长达半年的时间内，保供团队全力以赴，从梳理需求、组织询价、合同签订到履约，尤其在项目实施阶段进一步细化分工，将责任落实到人，形成事事有人为、人人有可为的积极氛围。\n       在公司领导的高度重视下，团队齐心协力，认真贯彻执行公司管理理念，坚决以“从干中学，在学中干，不断提高项目管理能力”为契机，把进度、质量、安全文明施工有机结合，通过加强协调管理，安全、保质地完成了各项任务，确保进度计划，并为公司锻炼、培养了一批在工程施工、质量安全、生产经营等方面的专业管理人员。\n       “新环境、新挑战、新模式”是该项目实施过程中的三大特点：\n       “新环境”——工作环境由原来物资采购前端的寻源、比价、供应，进一步延伸至使用现场的组织安装和调试；“新挑战”——项目时间紧、任务重，前期没有给予现场勘察时间，许多问题都需要现场边实施边调整边改造，给项目的顺利推进带来很大挑战；“新模式”——由公司办公模式切换至项目驻场模式，直接对接业主、监理、设计及施工总包、分包。\n       该项目家具供应体量大、生产周期长、组装周转空间条件严苛，团队及时跟进现场施工进度计划，根据装饰装修验收计划科学合理地进行生产任务的编排，尽量做到家具摆放无缝衔接。在家电进场后，因缺少电器安装的具体点位图，只能通过电源布设和平面布局图，结合房间使用功能来判断家电设备的具体安装位置，通过“筛子法”从普遍问题到特殊难题逐个击破，合理利用现场各方生产力量保证完工进度。后期收尾验收阶段，团队全体成员更是化身“保洁员”“保安员”，撸起袖子打扫卫生，不分昼夜值守，确保现场成果满足验收条件，充分发挥了艰苦奋斗的精神。\n       特供团队秉持服务初心，迎难而上，成功服务重大项目，既彰显了企业社会责任，也通过自身的努力，体现了作为上海建工一员，坚定维护集团上市公司形象的责任担当。电商公司也将继续不忘初心使命，勇攀高峰！\n       ",
        },
        {
          name: "合力攻坚，描绘“云上建工”精彩画卷，羿云科技",
          description:
            "数字化转型是上海建工“三大创新转型战略”之一。集团领导高屋建瓴地提出要打好数字赋能攻坚战，要以应用、监管、赋能为导向，打造企业版的“一网通办”、“一网统管”和“一网赋能”，建设智能、集成、创新、互联的“云上建工”。羿云科技是集团信息化、数字化建设的专业力量，建设“云上建工”，羿云科技当仁不让。\n       从无到有，“云上建工”的开发要在不到一个月的时间内完成，彼时展厅还是一片烟尘弥漫的装修工地，用于展示的大屏也只能在效果图上一窥踪影。工期紧，条件艰苦，是“云上建工”建设的第一个挑战。公司党支部书记、总经理钱芸亲自指挥，带领公司领导班子，协调研发部、智慧工地业务部和运行维护部，投入到“云上建工”的开发工作，加班加点挑灯夜战，讨论设计方案，梳理场景数据，开发测试部署。在展厅部署测试的时候，还要戴着防尘口罩，在嘈杂繁忙的装修工地上，蹲下来敲电脑，仰起头看大屏。尽管艰苦忙碌，参与项目的同事没有人叫苦叫累，是奋斗者精神激励着他们，战胜重重困难，全力以赴按时完成“云上建工”的建设和交付。\n       “云上建工”集成了包括智慧工地在内的十余个场景。各家单位提供的场景模式和数据五花八门，要在短时间内做好集成，要解决超大屏适配、数据整合、统一认证、页面开发标准等各种问题。集成困难是“云上建工”建设的又一个挑战。项目团队中的党员干部发挥先锋模范作用，带领技术骨干和开发实施人员，勇挑重担，攻坚克难。研发部不惧挑战，积极攻关技术难题；智慧工地业务部密切联系业务场景，确保完美展示效果；运行维护部协调资源保障，让系统运行稳定流畅。面对困难和挑战，他们有着强烈的责任感和使命感，深知自己的努力和付出对于实现目标至关重要。\n       “云上建工”如期在展厅华丽开启，优雅的色调、精致的画面、流畅的交互与严谨的数据、专业的场景、硬核的科技交相呼应，相得益彰，体现了羿云科技为集团数字化转型战略实施书写的浓墨重彩的篇章，展现了上海建工工匠精神的传承。",
        },
      ],
    };
  },
  methods: {
