          name: "胡宗海，男，1990年出生，中共党员，大学学历，现任安装集团第四工程公司总经理助理",
          description:
            "广州白云机场三期扩建工程工地现场，整个工地满是穿梭忙碌的身影，这其中就有安装集团第四工程公司项目部的建设者们，他们在项目经理胡宗海的带领下，栉风沐雨、昼夜兼行，躬身践行集团“全国化”战略，在强手林立的白云机场工地现场矗立起了上安的品牌形象。\n       广州白云机场三期扩建工程为重点民生工程，也是中国民航历史上规模最大的改扩建工程。针对不停航施工、机电系统先进繁杂、质量定位高、高空作业和交叉施工频繁造成安全管控难度大等难点，胡宗海带领团队依托安装集团在浦东、虹桥、天府等国内一流机场建设的丰富经验，充分发挥专业优势，不断打磨细节，高质量推进工程建设。\n       由于施工区靠近停机坪，为满足不停航施工的各种要求，胡宗海带领项目成员制订了科学详尽的方案，分门别类对防飘浮物、防烟、防火、防闪光、防尘、防震、防异味、防噪音等落实了严格的措施。为提高安装一次成型率，他带领团队积极应用预制装配式安装工艺，通过建设风管预制加工厂，灵活设置管道预制加工区，为现场装配式施工提供及时保障。同时，通过大量使用剪刀式液压升降作业车和使用高空作业车、管道全位置自动焊机、新式弯管加工装备、压槽机等先进施工装备，进一步提高了现场施工工效。\n       2024年1月，广州白云机场三期扩建工程交通中心及停车楼机电安装工程开工。建设初期，胡宗海带领项目成员开展BIM建模工作，在停车楼西区划定500平方米空间作为样板区，仅用5天时间完成BIM建模、深化设计出图、现场安装工作，经现场AR验评达到实模一致要求，满足了业主方的智能建造协同管理平台要求。\n       其实，安装集团早在千禧年初就曾承接新广州白云机场的建设任务，二十年的渊源给了胡宗海压力与动力。胡宗海体会到一颗匠心跨越时空、薪火相传，在新老交替、接力建设中，他始终被激励着：为品牌而战。",
        },
        {
          name: "张想，男，1984年出生，中共党员，大学学历，现任安装集团工业事业部副总工程师",
          description:
            "在这人迹罕至、浩瀚无边的荒漠戈壁上,安装集团工业事业部副总工程师张想凭着一股韧劲、钻劲、拼劲，熟练掌握了钍基熔盐堆涉核材料生产与加工、设备设计与制造、实验堆设计与系统集成等相关技术，甚至在关键技术上实现了突出创新，把奋斗的足迹留在了先进核能领域。\n       2MWt液态燃料钍基熔盐实验堆安装项目，是世界首座小型模块化钍基熔盐实验堆，也是面临熟悉核能新领域、掌握高精尖技术、自然环境恶劣等诸多难题的“国字号”工程。张想时任钍基熔盐实验堆安装项目总工程师，他将全部精力倾注到项目研究和实验中，通过自我加压、刻苦钻研,一路攻坚克难，以专业能力助力项目顺利实施。\n       针对戈壁滩上的洁净厂房建设要求，他通过设计专用工装，采用数字虚拟仿真技术，实现了高精度、高洁净度的装配目标。为确保精密设备就位的稳定性，他通过创新优化吊机、运输车等机械制动方式，运用专项监测手段，有效提高了设备运输、吊装、安装等全过程施工的平衡性，确保了设备的平稳就位，也为后续小型模块化反应堆顺利“安家落户”提供了参考。\n       为了展示企业在核能项目中的建造水平，张想倾注了全部的精力投入到项目研究和实践中，建立了从基础研究、实验验证到工艺实施的全过程工艺控制体系，并通过对关键性技术、工序、环境进行模拟演练，形成了核心堆本体一体化安装、高温回路安装、熔盐堆高精度测量等专项技术，令业主、同事刮目相看。\n       从嘉定的仿真平台到武威的实验堆，张想经历了新材料、新设备、新工艺应用以及安装、调试等关键技术攻关的全过程。不同的地方、不同的堆型、不同的团队，始终不变的是他兢兢业业、勤于钻研的态度。在‌荆棘载途的科研路上，张想经受了漫天黄沙的洗礼，用自强不息的精神和百折不挠的毅力，在戈壁滩上高奏了一曲奋斗的凯歌。",
        },
        {
          name: "马晓云，男，1984年出生，中共党员，大学学历，现任基础集团第一工程公司S4公路奉浦东桥及接线工程施工1标\n       项目经理",
          description:
            "马晓云同志先后从事技术员、技术质量科长、项目工程师、项目常务副经理、项目经理等岗位工作，曾参与泖港大桥、五朱公路等项目建设。\n       在项目建设管理中，他全力以赴推进项目施工进度，在保证施工质量安全的基础上，抢抓各个节点。面对该工程水上施工、起重吊装等作业量大的情况，他以安全工时赛为抓手，做好安全风险分析和控制，为项目平稳有序推进保驾护航。\n       对于项目面临超高标准的预制精度，多工况高风险安装工艺等难题，时间紧、任务重，他结合实际情况对施工任务进行统筹安排，带领项目团队先后完成栈桥及水中钢平台施工、主桥水中立柱首件混凝土浇筑、主桥节段梁首件吊装、主桥合龙等多项工程重要节点。\n       在技术工作方面，他结合工程遇到的实际问题，带领团队深入研究，积极推动技术创新。S4项目位于航道繁忙的黄浦江中，施工区域紧挨运营的高速公路，针对跨黄浦江桥梁工程施工特点，他带领团队深入研究“四新技术”，该项技术为S4项目主桥顺利合龙奠定了坚实基础。\n       在项目经济管理工作方面，他坚持因项施策精打细算，开源节流降本增效，积极推动项目成本控制管理工作，优化施工技术方案，控制项目成本，确保项目经济效益。在项目施工过程中，采取超验工计价方式，确保项目资金保存量与工程顺利进行。\n       在主桥施工高峰期，争取“安全月”、“质量月”观摩会；先后举办特殊工种工人技能比武大赛、安全生产技能比武活动、水上应急救援演练；承办了建工集团“测量杯”大赛；同“交通大学、河海大学”搭建学习平台；邀请华坪派出所深入项目开展防诈骗宣讲等活动。\n       S4项目团队在以马晓云为核心的领导下，获得了上海市重点工程事实立功竞赛“优秀团队”、“特色项目”；上海市“文明工地升级示范版”等多项荣誉。",
        },
        {
          name: "杨成龙，男，1980年出生，中共党员，大学学历，现任基础集团第五工程公司上海轨道交通市域线机场联络线JCXSG-7标\n       项目经理",
          description:
            "上海轨道交通市域线机场联络线工程JCXSG-7标作为建工集团迄今为止14米级以上超大直径盾构区间单隧道长度最长、也是隧道总长度最长的项目。2021年，工程处于基坑开挖阶段，面对工期紧张、周边地质环境复杂、杨高南路外环线等重要构建筑物保护要求极高等难点，而基坑开挖的节点能否顺利完成对当年盾构能否顺利下井并完成安装调试具有决定性影响。杨成龙同志当机立断，根据开挖情况增派长臂挖机和滑臂挖机，确保了多层面放坡开挖同时进行，有效的推进了工程进度。\n       在2022年疫情封控期间，他更是扎根项目、统筹安排，确保了项目部疫情防控工作的稳定、受控。同时，他积极推动复工复产工作，是第一批申报复工复产白名单的工程。\n       项目全面复工后，他始终靠前指挥、全程督战，顺利完成了刀盘吊装下井、盾构始发后立即穿越的复合风险等技术难题，更是一举创造了月进尺596米，平均日进尺19.2米的14米级全预制大直径泥水盾构国内最快的施工记录。获得了各方的高度赞誉。\n       市域铁路建设响应国家低碳、绿色的号召，成为首条预制化率100%的盾构线路。因此，隧道内部结构首次采用了底部弧形件、中隔墙等超重、超大的预制构件，为了确保这些预制构件在安装、精调的过程中安全、高效，杨成龙同志大胆开拓创新，积极牵头组织技术人员和班组骨干进行科研攻关和QC活动，自主研发了底部弧形件精调设备和中隔墙拼装设备，并就以上两项关键技术完成了论文撰写及专利申报，其中《大直径隧道内超重预制中隔墙拼装方法》获得授权。\n       项目部也因此获评2021年度浦东新区青年突击队、2022年度上海市特色项目、2022年度上海市交委文明工地、2022年度上海市工人先锋号等多项荣誉。\n       ",
        },
        {
          name: "陆春，男，1982年出生，中共党员，大学学历，现任机施集团第一工程公司副总经理",
          description:
            "陆春同志分管机施集团浦东机场板块钢结构、屋面、幕墙项目施工。自2003年进入单位以来，先后参与了上海浦东、虹桥机场和世界会客厅等十几项工程建设。\n       在“世界会客厅”建设中，面对场地狭小，管线复杂、施工周期短等不利因素，他牵头攻克了“桩位偏差”、“塔吊基础搭设”和“大跨度桁架吊装”等多项技术难题，首次应用顺逆结合钢结构综合体快速建造技术，即“地下逆作、地上顺做”形式，仅用时4个月完成近2万吨钢结构施工。他还运用“钢结构幕墙两个一体化”管理思路，创新使用幕墙龙骨单元化技术，显著减少现场工作量，提高施工效率。春节期间，他带领600人团队坚守岗位，即使自己发烧40度也未离开，确保了4.5万平方米立面幕墙的施工质量和进度。\n       陆春同志重视管理创新，带领团队完成浦东机场区域内国内首座跨运营中磁悬浮与地铁双轨交段上方钢连廊合拢施工。针对钢箱梁及上部大型钢连廊结构形式复杂、跨度大、且需保证双轨交线运营不受影响等难题，他带领团队采取大跨度钢箱梁少分块整体吊装的工艺、研发了大型钢连廊整体提升滑移的施工工法，优化了大型移动提升架的轨道基础转换结构设计。基于二十年对两大机场不停航施工的经验，他参与课题研发，分享经验成果，不仅为机场施工提供了技术保障，也为国内其他不停航状态下大型机场改扩建项目提供了宝贵的技术资料和详实的各类数据。过去三年，他申请专利4项，完成 2项课题研究。今年3月，在过度劳累导致住院手术后，他很快主动返回工作岗位，凭借坚韧不拔的意志和科学的管理的手段，确保机场项目无管理空白、无安全事故、质量有保障。\n       在他的带领下，团队中1人荣获建工集团期望奖，3人荣获建工集团优秀建设者，3人获评机施集团新长征突击手、期望奖，集体连续两年获评建工集团优秀集体，实现了“出精品，出精英，出精神”的效果。\n       ",
        },
        {
          name: "雷丽芳，女，1970年出生，群众，大学学历，现任装饰集团国内市场部总经理",
          description:
            "2021年，雷丽芳同志凭借出色的表现，从成都调至上海，担任装饰集团国内市场部总经理。4年来，团队由最初的1人，逐步扩展至现今的40多人。其中的多人为成熟的属地化经营精英，部门积极营造和谐团队氛围，紧密融入上海建工文化。\n       双管齐下抢占市场高地。2023年，她主导成立机场事业部，成功中标广州白云、西宁曹家堡、烟台蓬莱、合肥新桥机场等多个地标项目，开拓了机场版图。随着成都科学馆、安吉文化艺术中心等重大项目的中标，进一步推动文化场馆事业部的成立。这些项目的实施显著提升公司的品牌影响力，为新型领域市场的开拓奠定坚实基础。\n       重量级工程见证市场影响力。她长年奔波于各个城市，每年飞行超百次，平均每周出差5个城市，展现其非凡毅力和高效的客户沟通力。热情、积极、乐观的态度，让她在装饰领域屡创佳绩。相继在青岛、济南、武汉、厦门、乌鲁木齐、合肥、芜湖、深圳、广州、西安、北京、杭州、成都、南京、徐州、淮安等地成立了16个办事处，基本覆盖了战略目标市场区域。通过对标国际一流，深耕大湾区，在深圳工务署，陆续中标了深圳音乐学院、深圳宝安区人民医院、深圳书城湾区、中山大学附属第七医院、天津大学佐治亚理工深圳学院校区等多个知名项目。今年更是中标深圳建市以来最大体量的8.4亿深圳国际交流会议中心项目，彰显不凡实力。\n       四年实现合同额飞速增长。她以卓越的领导力和不懈的努力，为公司创造了显著的经济效益和市场拓展成果，带领团队超额完成了公司下达的各项任务指标。从2021年完成的4.41亿元到2022年完成的6.41亿，再到2023年完成的11.25亿元，截至今年9月已完成14.77亿元。这些数值的快速增长，充分证明了雷丽芳同志在市场开拓和业务管理方面的卓越才能。同时，她勇于突破自己，积极尝试探索海外市场，目前正在跟踪接洽新加坡“第一高”地标建筑以及樟宜机场T5航站楼等诸多潜在项目。\n       ",
        },
        {
          name: "俞杰，男，1986年出生，群众，大专学历，现任装饰集团第七工程公司副总经理",
          description:
            "上海船厂1862、国家会展中心、世界会客厅、顶尖科学家论坛永久会址、成都科幻馆等大型文化场馆都有俞杰同志的建设身影。\n       2008年10月，俞杰同志入职上海建工装饰集团，一路从资料员、施工员、项目工程师、生产经理，成长为第七工程副总经理，扎实的一线业务能力为他现如今的生产管理工作打下了坚实的基础。\n       2021年，“世界会客厅”项目，他担任1、3号楼标段负责人。面对如此高标准、严要求的政治性任务，他敢于打破以往工作思路，将工作面全面切分落实到人。通过每天的饭点时间，与团队人员一起商讨后续工作安排，紧紧围绕“围点打援”的工作理念逐个解决，按照节点目标逐一击破。\n       2022年，他收到指令进驻顶尖科学家永久会议会址项目，并担任项目负责人一职。面对新的岗位和挑战，他利用“世界会客厅”项目成功实践的经验，带领团队在设计阶段就全面介入，在各项工作准备阶段紧密围绕装配化施工路线前行。面对大空间施工，项目一开工就完成1：1样板制作，得到各方面的认可和肯定。在主要空间进入施工正轨以后，他将主要经历集中在处理周边辅助、卫生间等既花时间，又需各专业协调配合的较复杂的地方，按时按质完成了项目各项建设。\n       2023年，他接到命令出发远在1700公里外的“成都科学馆”项目，这是国际顶级建筑大师扎哈团队设计的一个地标性建筑。他带领项目团队采用全BIM指导施工，利用数字化软件形成参数化数据，对场馆进行整体建模并加工推进。在150天超短工期内，与国际大师紧密携手，完成了近4万平方米的室内全专业和6.9万平方米的室外超大体量梦幻级科幻场馆的毫米级精致建造。\n       这几年，他在各个重大项目上不断历练和成长，以超强执行力和称心的服务，带领团队助力项目收获“集团精品观摩工程”、“放心项目部”、“白玉兰”、“全国装饰奖”、“鲁班奖”等多项荣誉成果。\n       ",
        },
        {
          name: "赵彦，男，1982年出生，民盟，博士学历，现任园林集团园林设计院副院长 总工程师",
          description:
            "赵彦同志获中国风景园林学会科学技术奖规划设计1项、上海市优秀风景园林规划设计奖1项、上海市优秀国土空间规划设计奖1项、上海市优秀工程咨询成果水平2项、中国自然教育大会文创产品设计铜奖等奖项。\n       深入贯彻习近平生态文明思想，立足本职岗位，发挥专业优势，在援疆、援藏、大湾区、革命老区对口合作等重大、难点项目中展现非凡的攻坚能力，足迹遍及18个省、自治区、直辖市，开拓市场份额，树立形象口碑。\n       围绕人与自然和谐共生，锚固企业核心竞争力。承担上海市十四五公园布局与上海公园城市规划并逐渐落地。《基于规划引领的环城生态公园带数治平台顶层设计与场景应用示范》课题，推动公园智慧园林数字化平台建设。带领团队结合生物多样性OECMs理念，完成总院首个国土空间生态修复规划，首个全域土地综合整治生态修复规划，开拓新市场。                                                                                                                                                                                                                                                                                                                                                                                               \n       深度参与长三角一体化绿色科技示范楼建设与运维，结合市科委碳中和课题、建工集团运维课题，基于李子园公园碳计量，开展公园绿地碳普惠及多项行业低碳团标编制，连续承办两届中国自然教育大会上海分会场活动，通过自然教育学校、科普教育基地、环境教育基地三大场景平台建设，发挥楼园社会效益与品牌价值，推动绿色大楼迈向“楼园一体”。\n       积极塑造企业品牌，挖掘企业红色基因与精神品格。在规划设计中坚守中华文化立场的自觉性与主动性，在黄浦区老城厢、嘉定秀野堂、喀什莫尔寺遗址等项目中，深入挖掘文化内涵，持续探索价值转译，提升企业新质文化生产力。\n       作为民主党派成员，建言献策，担任区政协社情民意专家，获得市政协，民盟市委的多次采用。担任最高检“益心为公”生态环境损害公益诉讼志愿者，参加诉讼听证环节，展现强烈的社会责任感。\n       ",
        },
        {
          name: "单正猷，男，1984年出生，中共党员，大学学历，现任建工总院建浩公司董事长、总经理",
          description:
            "单正猷2007年毕业自同济大学建筑工程系毕业后即加入集团，他认真履行“一岗双责”，担任总监的上海轨交18.5标，与团队成员接力作战，通过24h不间断旁站，确保了“超长联络通道”双侧同时冰冻、同步开挖的质量安全；参与全国首例“新型管片连接件错缝拼装”的盾构施工科研项目，实现“从无到有”的技术突破。担任总监的张江科学之门西塔项目，遭遇疫情封控，他始终坚守逆行出征时的炽热初心,强忍身体疼痛、冬夏更替的不适，与项目组同吃同住封控近3个月，确保了拥有1500多人项目无“新增”。凭借出色的组织协调能力和沉着冷静的心态，成功解决了深基坑施工、超高层和超大型项目组织管理中的诸多难题，确保了项目的顺利进行。围绕市场环境严峻挑战，他不畏艰难、改革创新，始终围绕“平台、人员、教育”三大核心，通过“稳、扩、拓”战略，有效推动了建浩公司监理、造价、全过程咨询和新业务板块的发展。2023年，完成合同额3.12亿元，同比增长20.6%；收款额2.49亿元，同比增长9.4%，创历史新高。\n       在传承中创新，聚焦“四化”融合战略，主导建立公司多个研究中心，深入研究行业热点与企业难题，形成系列创新性的研究成果。并引领公司承接松江区首个建筑师负责制项目，拓宽了工程咨询业务领域。他主导构建是“公司经营管理信息系统”实现项目全流程在线管控，显著提升企业管理效率。数字化转型方面，他积极响应BIM技术政策，大力推动其在城市建设中的应用，并参与编撰《保障性住房BIM技术应用监管》专著，并成功申报上海市住建委《基于BIM的全过程数字交付关键技术》重点课题，为企业技术创新注入新动力。\n       他多次与班子成员讨论并通过了职工体检等多项职工福利方案，通过参与职工运动会、党工团活动等方式与员工保持密切互动。在推动公司发展的过程中，他亲自带领团队外出走访调研17次，广泛征求意见和学习取经，集思广益，推进改革，为长远发展奠定了坚实基础。",
        },
        {
          name: "周建诚，男，1966年出生，中共党员，大专学历，现任建材科技市政构件公司党总支副书记、总经理、总经济师",
          description:
            "周建诚带领市政构件公司全体职工积极响应国家发展战略，推进企业高质量发展，先后参与了北横通道、上海大歌剧院、嘉闵高架、S3、17号线、崇明线、浦东机场联络线以及外环东抬升等重大工程项目建设，连续3年产量逐级提高，2023年完成了34万方，创历史新高，预计2024年将达到年产40万方的目标。公司先后荣获上海市工人先锋号、建工集团“精品杯”立功竞赛优秀集体等称号。\n       周建诚坚持以技术创新引领团队破解生产制作难题。他始终以事不避难、迎难而上的姿态面对施工难度高、工期紧迫等诸多挑战，他精细安排生产任务，精准调度各方资源，确保每一个项目均能按时保质保量交付，赢得了行业内外的高度评价和认可。成功生产了世界最大直径管片——静安区智慧车库管片、国内最高强度构件——上海大歌剧院UHPC悬挑梁、上海埋深最深管片——机场联络线管片以及行业外观要求最高构件——浦东机场清水挂板，极大丰富了预制构件产品体系，打响了企业品牌。他以精益求精的态度，不断在预制构件生产领域取得创新性、突破性成果，推动公司实现了转型升级。\n       周建诚注重智能装备、新兴技术的运用，通过“智慧工厂”建设，完善“上建砼构”预制构件可视化生产管理系统，通过应用弯弧矫正一体机、钢筋骨架升降平台、自动盖板装置等智能设备，提高了生产效率和精度；应用管片智能抹面机器人、盾构管片平面网及立体网焊接机器人等，提升了企业的数字化和智能化水平，提高预制构件生产能级，推动行业智能化、信息化发展。他还坚持“以项目带团队”的理念，积极发挥“周建诚职工创新工作室”作用，培育出一批优秀的青年技术人才，成为企业的中坚力量,工作室被评为“上海市职工创新工作室”，职工的技术创新成果先后获得多项发明专利和实用新型专利，团队和个人也多次荣获各类全国、上海市、建工集团级科研奖项。\n       ",
        },
        {
          name: "高如意，男，1985年出生，中共党员，大学学历，现任外经集团厄特分公司工管部经理、贝雷扎电站项目经理",
          description:
            "高如意同志自2013年加入上海外经以来，长期坚守在非洲等海外一线，先后参与并负责涉农工厂、阿科达特公路、金矿地采及贝雷扎电站等多个项目的建设和运营，其中，他担任项目常务副总经理的科卡金矿地采项目将“中国标准”及“中国制造”带入厄特矿业领域，开创了厄特地下采矿的历史先河。\n       在厄特涉农工厂项目中，面对当地高温、缺水的紧迫难题，高如意带领团队深入探索新技术，最终引入了冷风机电热融霜技术，大幅度提高了项目的竞争力和市场价值。由其牵头完成的项目课题《高温环境冷库库体保温工程施工技术研究与应用》获得了“2018年度建工集团科技成果奖三等奖”。科卡金矿地采项目作为厄特第一个地下采矿项目，难度重重。高如意带领团队在现有技术的基础上进行创新性的应用和拓展，开发出一套全新的14步循环流程化施工工艺，确保了井下作业的安全和质量。在贝雷扎电站项目中，面对当地高原盘山、百吨吊装等难题，经反复研究后，高如意带领团队果断选用中船产品“船机陆用”，再次将中国技术应用到非洲市场，用实力为中国产品赢得良好口碑。\n       在厄特涉农工厂等中厄合作项目中，高如意安排中国师傅对当地员工“帮扶带教”，先后已解决了400多人次当地百姓的就业问题，并在建设之余，带领团队为当地居民净水和修路，助资当地教育事业，积极推进文化交流。\n       在项目执行过程中，高如意根据每位青年员工的具体情况，定制个性化培训计划，确保培训内容与他们的职业发展需求紧密相关，并制定明确的职业发展路径。引入绩效激励机制，在项目上建立起培养与激励体系，发挥属地员工优势，促进外经集团与当地社会的和谐发展，提升项目的实施效率和质量。同时打破部门壁垒，促进不同部门之间的沟通与合作，以实现资源的优化配置和问题的有效解决，提高项目实施效率与创新水平。",
        },
        {
          name: "崔建昌，男，1989年出生，群众，大学学历，现任天住集团华惠安信项目经理",
          description:
            "崔建昌现任呼市机场公区精装修四标段项目经理。该同志曾参与多个国家级、市级优质工程，承建了天津中新生态亲老公寓、天津市公安局业务技术用房、北京新机场旅客航站楼、雄安新区容东片区G2组团安置房等重大精品项目，参建项目多次荣获中国建筑鲁班奖、中国建筑装饰奖等奖项。该同志作为公司优秀青年项目经理代表，所在直属项目部曾荣获全国青年文明号、天津市青年文明号、天津市青年安全生产示范岗等荣誉；同时，曾荣获上海建工集团优秀青年项目经理、天住集团新长征突击手，并带领团队曾荣获上海建工集团“共青团号”青年突击队荣誉。\n       2019年，参建北京大兴国际机场精装修九标段工程，负责指廊装修工程东南指廊的施工。该同志面临施工环境不熟悉，工程难度系数大，工期紧，任务重等重重考验，积极整合公司各类资源，合理组织，积极策划，前期熟悉场地及图纸，提前制定施工预案，为打好这场硬仗做充分准备，建成后对于推动国际交通枢纽建设、促进经济发展、推动技术创新、体现环保节能、展示文化、影响政策、提供社会服务具有重要意义。\n       2020年，该同志担任雄安新区G2安置房项目负责人，这是公司服务京津冀协同发展国家战略，向雄安迈进的第一步。在整体环境高压状态下，一刻毫不松懈，争分夺秒，11月2日进场，12月5日完成首个样板间，并且高品质呈现给政府和雄安集团，在整个安置房片区名列前茅，多次登上国内主流媒体及央视新闻，成为拍摄的必经之地。\n       目前，该同志目前践行上海建工集团“全国化”战略部署，服务于呼和浩特新机场精装修工程，项目建成后将成为国内重要干线机场，是我国向北开放的桥头堡，对促进内蒙古自治区经济社会高质量发展，具有重要意义。在施工管理中，因精细化的管理风格与工作成效，甲方授予了“质量工匠之星”。",
        },
        {
          name: "黄盛，男，1986年出生，中共党员，大学学历，现任天住集团总承包公司第二项管部总工程师兼项目经理",
          description:
            "黄盛，2008年10月参加工作，高级工程师，一级建造师、一级造价工程师，毕业于天津大学建筑与土木工程专业，从事建筑施工工作16年。在工作中他始终脚踏实地、勤勉负责，曾获上海建工集团优秀项目工程师、优秀共产党员及天住集团的“五一劳动奖章”，充分展示出新时代青年建筑工程干部的典范风貌。\n       参加工作十几年，黄盛同志始终坚持扎根一线以项目为战场。河北实验小学腾退工程更新改造后为全国知名网红书店“钟书阁”在天津的首家分店，建成后将打造成意式风情街核心区文化地标。项目在建设中，面对施工工期紧、冬季大雪恶劣天气等诸多不利因素下，带领项目团队科学统筹、精心策划，灵活调整施工组织，调整各工序穿插时间，在规定节点工期内高质量完成施工任务，确保工程顺利实施。\n       为了完美呈现钟书阁外檐欧洲传统三段式建筑风格，黄盛利用定制异形陶砖组成干挂幕墙工艺技术，通过对砖型错落有致的排布，完美呈现了墙体的凹凸造型，该工艺在天津乃至全国尚属首次应用。且整个外檐砖共涉及80套模具，400余种规格，15万余块，为项目建设带来极大挑战。黄盛带领技术团队发挥精益求精的工匠精神，利用BIM技术建模，完成各专业总体模型20余个、分层深化设计模型10余个、施工辅助节点模型10余个，历时3个月完成外檐幕墙深化工作。同时利用revit制作了砖模的精确三维图纸，指导生产厂家制造模具，显著缩短了生产周期，同时提高了砖模精度，降低了废料率并节约了生产成本。\n       “顺境时不骄傲，逆境中不气馁，将工作做到极致。”这是黄盛的工作追求，也是他未来奋进征程中不懈坚持的职责使命。在今后的工作中，黄盛将继续弘扬“事不避难、迎难而上、追求卓越”的“奋斗者精神”，坚守本职岗位，用责任与担当践行初心和使命，继续打造一项项精品工程，为企业高质量发展添砖加瓦。\n       ",
        },
        {
          name: "曾鸿叶，男，1983年出生，中共党员，大学学历，现任海外部蒙古乌兰巴托至达尔汗公路扩建工程第四标段项目经理",
          description:
            "曾鸿叶同志于2009年6月入职海外事业部，先后参与了柬埔寨湄公河大桥，柬埔寨62号公路、59号公路、6号公路和蒙古巴彦洪格尔至巴依扎格桥129.4公里道路等项目建设，现任蒙古乌兰巴托至达尔汗公路扩建工程第四标段项目经理，累计海外工作长达15年。他长期从事海外公路项目施工管理工作，从最基础的施工员做起，逐步成长为项目经理。\n       2020年9月，在新冠疫情在蒙古国蔓延的情况下，曾鸿叶带领项目筹备工作组逆行入蒙，项目前期工作受制于疫情影响开展难度极大。冬季的蒙古国最低温度可达到-50摄氏度，室外寒风刺骨、白雪覆盖，项目驻地是一栋简陋的砖砌矮楼，墙皮脱落，管道生锈，没有暖气和热水，条件异常艰苦。曾鸿叶同志带领工作组排除万难，完成了导线复测、用地用水审批、机械调运、银行开户、人员进场等工作，为项目按期开工创造条件。\n       在三年多的紧张施工过程中，伴随着俄乌冲突不断升级，地缘政治复杂交织，道路施工所需的沥青从俄罗斯进口受阻，蒙古国水泥产能不足极为短缺、价格飙升，材料、机械设备等因口岸关闭，都堆积在边境，迟迟无法到场，现场施工进度一度严重滞后。他带领项目团队积极协调各方资源，保障施工所需水泥、柴油及沥青等主材的供应，及时调整进度计划，把控施工节奏。\n       乌兰巴托至达尔汗公路是连接蒙古首都乌兰巴托和蒙古第二大城市达尔汗的主要通道，为蒙古国重点建设工程，自开工以来备受关注，蒙古国政府和社会各界对该公路的早日通车寄予了强烈期望。为了不影响工作，他每天连夜驾车300公里奔波于乌兰巴托和达尔汗项目现场。疫情两年里，他以大局为重，放弃回国休假与家人团聚，孩子生病他也无暇陪伴，只能通过视频通话与家人商量治疗方案，一心扑在项目第一线。2022年10月，项目最终按期全线贯通，他也被蒙古国交通运输部授予最高荣誉“蒙古交通运输贡献奖”。",
        },
        {
          name: "张亮，男，1987年出生，中共党员，大学学历，现任海外部柬埔寨分公司副总经理",
          description:
            "张亮 2009年入职海外事业部，现任海外部柬埔寨分公司副总经理兼老挝办事处主任。张亮同志具有近15年海外经历，先后参与了援巴基斯坦巴中友谊中心，援几内亚首都五万人体育场和总统府改造、援中老铁路搬迁安居村等近10个项目。他负责的几内亚和老挝项目，分别在2015年度和2022年度获得集团突出贡献奖。\n       2020年初，为践行共建“一带一路”倡议，张亮在疫情爆发之下，服从组织安排，逆境出征，踏上了老挝这片陌生环境。面对百年变局和全球疫情带来的诸多困难和挑战，他带领现场仅有的几名青年同志，在国内资源无法跟得上的情况下，克服老挝当地封城管控、省道关闭、航班停运、洪水灾害、物资人员匮乏等困难，秉承当地问题当地解决的理念，通过全属地化经营和管理，2020年至2023年，先后承接并顺利实施了4个国际总承包项目，1个援外本土化项目，涵盖老挝中部、北部、南部等不同地理区域，确立了以 “属地化生产”、“以项目促经营”的老挝市场经营发展思路。在他所实施的项目中，其中老挝水资源环境部大楼（世行资金）荣获了“老挝国家部长级优秀项目奖”，老挝沙湾拿吉大学新校区项目（亚行资金）“老挝亚行优秀项目奖”，援老挝中老铁路搬迁安居村项目荣获了“老挝国家级突出贡献奖”。 \n       同时，为不断夯实柬老“示范区”稳健持续发展，张亮在老挝继续以“市场化”作为工作总原则，以“房建”为基本盘，坚持“质量第一、效益优先，“以现场换市场”，不断开拓新领域新部门新业务作为未来老挝发展的新方向。2024年成功中标了老挝丰沙里省乡村供水项目、老挝2号国道口岸设施升级项目、老挝万象加华银行总部改造项目等，为集团在老挝可持续高质量发展再次迈出坚实的一步。",
        },

        // 可以继续添加更多员工信息
      ],
    };
